<template>
  <div class="NumberLogin">
    <div class="topback"></div>
    <div class="NumberLoginTitle">自然保护区</div>
    <div class="NumberLoginContent">
      <div class="TopImg">
        <!-- <img src="@/assets/phone/AppVisitorHome/icon_apphome.png" alt /> -->
      </div>
      <ul class="NumberLoginContentBox">
        <li>
          <input
            type="text"
            v-model="CodeLoginData.phoneNumber"
            placeholder="请输入手机号"
          />
        </li>
        <li>
          <input
            type="text"
            v-model="CodeLoginData.code"
            placeholder="验证码"
          />
        </li>
        <li class="NumberLoginBtn" v-if="have" @click="CodeLoginBtn">登 录</li>
        <li class="NumberLoginBtn" v-if="!have" @click="loginFlagBtn('SignIn')">
          去登录
        </li>
        <li class="LoginProblem">
          <div
            @click="GetCode"
            :style="{ pointerEvents: canClick ? 'unset' : 'none' }"
          >
            {{ content }}
          </div>
          <div @click="loginFlagBtn('guarantee')">去注册</div>
        </li>
      </ul>
    </div>
    <div class="bottomTitle">广西雅长兰科植物国家级自然保护区管理中心</div>
    <div class="bottomback"></div>
  </div>
</template>

<script>
import { verificationCode } from "@/api/case/case";
import { validatorPhone } from "@/utils/validate";
import { login } from "@/api/case/case";
import { setToken } from "@/utils/auth";
import { Encrypt } from "@/api/tyrz/DES";
export default {
  props: ["loginFlag"],
  data() {
    return {
      CodeLoginData: {
        phoneNumber: "",
        code: "",
      },
      content: "发送验证码",
      totalTime: 60,
      canClick: true, //添加canClick
      have: true,
    };
  },

  methods: {
    CodeLoginBtn() {
      let _this = this;
      if (
        this.CodeLoginData.phoneNumber == "" ||
        this.CodeLoginData.code == ""
      ) {
        this.$message({
          message: "手机号和验证码不能为空!",
          type: "warning",
        });
      } else {
        if (validatorPhone(this.CodeLoginData.phoneNumber).flag) {
          let obj = Object.assign({}, this.CodeLoginData);
          obj.password = Encrypt(obj.password);
          login(obj).then((res) => {
            if (res.code == 200 && res.msg == "操作成功") {
              _this.$store.commit("SET_TOKEN", res.token);
              setToken(res.token);
              window.sessionStorage.setItem("SET_TOKEN", res.token);
              _this.$message.success("登录成功");
              //  _this.$router.push("/home");
              _this.$router.push("/apphome");
            }
          });
        } else {
          this.$message({
            type: "warning",
            message: validatorPhone(this.CodeLoginData.phoneNumber).message,
          });
        }
      }
    },
    loginFlagBtn(val) {
      this.$emit("loginFlagFn1", { name: val });
    },
    // 验证码登录
    GetCode() {
      if (validatorPhone(this.CodeLoginData.phoneNumber).flag) {
        if (!this.canClick) return;
        this.canClick = false;
        this.countDown();
        verificationCode(this.CodeLoginData.phoneNumber).then((res) => {
          this.$message({ message: res.msg, type: "success" });
        });
      } else {
        this.$message({
          type: "warning",
          message: validatorPhone(this.CodeLoginData.phoneNumber).message,
        });
      }
    },
    countDown() {
      this.content = this.totalTime + "s后重新发送";
      let clock = window.setInterval(() => {
        this.totalTime--;
        this.content = this.totalTime + "s后重新发送";
        if (this.totalTime < 0) {
          window.clearInterval(clock);
          this.content = "重新发送验证码";
          this.totalTime = 60;
          this.canClick = true; //这里重新开启
        }
      }, 1000);
    },
  },
  mounted() {},
};
</script>
<style lang='scss' scoped>
.NumberLogin {
  width: 100vw;
  height: 100vh;
  font-size: 4vw;
  // background: linear-gradient(116deg, #41bb64, #0a4fa0);
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  .topback {
    height: 40%;
    width: 100%;
    background: url("../../../assets/phone/AppVisitorHome/top_back.png")
      no-repeat;
    background-size: 100% 100%;
    position: absolute;
    top: 0;
  }
  .bottomTitle {
    font-size: 12px;
    color: #d1d1d1;
    position: absolute;
    bottom: 10vh;
    font-weight: 400;
    text-align: center;
  }
  .bottomback {
    height: 5%;
    width: 100%;
    background: url("../../../assets/phone/AppVisitorHome/bottom_back.png")
      no-repeat;
    background-size: 100% 100%;
    position: absolute;
    bottom: 0;
  }
  .NumberLoginTitle {
    font-size: 8.5vw;
    font-weight: 800;
    text-align: center;
    padding-top: 4.47vh;
    box-sizing: border-box;
    margin: 0 auto;
    color: white;
    position: absolute;
    text-align: center;
    top: 0;
    width: 100%;
  }
  .NumberLoginContent {
    width: 85vw;
    // height: 78vh;
    background-color: white;
    border-radius: 2vw;
    position: absolute;
    top: 20.29vh;
    padding-bottom: 5vh;
    border: 0.05vw solid #00000018;
    box-shadow: 0vh 0vw 5vw 3vw #00000018;
    .TopImg {
      width: 20vw;
      height: 20vw;
      margin-top: 0 !important;
      position: absolute;
      top: -10vw;
      left: calc(50% - 10vw);
      background: url("../../../assets/phone/AppVisitorHome/icon_apphome.png")
        no-repeat;
      background-size: 100% 100%;
      img {
        width: 20vw;
      }
    }
    .NumberLoginContentBox {
      list-style: none;
      text-align: center;
      margin-top: 9.48vh;
      li {
        margin-top: 5vw;
        position: relative;
        input {
          width: 70vw;
          border: 0.4vw solid rgb(153, 154, 155);
          background-color: #c7c5c521;
          border-radius: 4vw;
          padding: 2vw 0 2vw 4vw;
        }
      }
      .NumberLoginBtn {
        width: 75vw;
        height: 6vh;
        background: linear-gradient(110deg, #41bb64, #0a4fa0);
        color: white;
        text-align: center;
        border-radius: 7vw;
        font-size: 5vw;
        font-weight: 400;
        line-height: 6vh;
        margin: 6vw auto 0;
      }
      .LoginProblem {
        width: 85%;
        display: flex;
        justify-content: space-between;
        margin: 6vw auto 0;
        color: #146296;
      }
      .TopImg {
        padding-top: 10vw;
        padding-bottom: 3vw;
        img {
          width: 20vw;
        }
      }
    }
  }
}
</style>