<template>
  <div class="box">
    <div class="SignIn" v-if="loginFlag == 'SignIn'">
      <div class="topback"></div>
      <div class="SignInTitle">访客管理</div>
      <div class="SignInContent">
        <div class="TopImg">
          <!-- <img src="@/assets/phone/AppVisitorHome/icon_apphome.png" alt /> -->
        </div>
        <ul class="SignInContentBox">
          <li>
            <input
              type="text"
              v-model="loginForm.userName"
              placeholder="请输入用户名"
            />
          </li>
          <li>
            <input
              type="password"
              v-model="loginForm.password"
              placeholder="请输入密码"
            />
          </li>
          <li class="SignInBtn" @click="LoginBtn">登 录</li>
          <li
            class="registerBtn"
            @click="loginFlag = 'guarantee'"
            v-if="appType == 'apphome'"
          >
            注 册
          </li>
          <li class="LoginProblem">
            <div
              @click="loginFlag = 'passwordRetrieval'"
              v-if="appType == 'apphome'"
            >
              登录遇到问题?
            </div>
            <div @click="loginFlag = 'NumberLogin'" v-if="appType == 'apphome'">
              短信验证码登录
            </div>
          </li>
        </ul>
      </div>
      <div class="bottomTitle">广西雅长兰科植物国家级自然保护区管理中心</div>
      <div class="bottomback"></div>
    </div>
    <NumberLogin
      :loginFlag="loginFlag"
      @loginFlagFn1="loginFlagFn"
      v-if="loginFlag == 'NumberLogin'"
    ></NumberLogin>
    <guarantee
      :loginFlag="loginFlag"
      @loginFlagFn1="loginFlagFn"
      v-if="loginFlag == 'guarantee'"
    ></guarantee>
    <passwordRetrieval
      :loginFlag="loginFlag"
      @loginFlagFn1="loginFlagFn"
      v-if="loginFlag == 'passwordRetrieval'"
    >
    </passwordRetrieval>
  </div>
</template>

<script>
import { Encrypt } from "@/api/tyrz/DES";
import { mapMutations } from "vuex";
import { login } from "@/api/case/case";
import NumberLogin from "./NumberLogin.vue";
import guarantee from "./guarantee.vue";
import passwordRetrieval from "./passwordRetrieval.vue";
import { setToken } from "@/utils/auth";
export default {
  components: { NumberLogin, guarantee, passwordRetrieval },
  data() {
    return {
      loginForm: {
        // 登录
        userName: "",
        password: "",
      },
      loginFlag: "SignIn",
      appType: "apphome",
    };
  },
  methods: {
    loginFlagFn(val) {
      this.loginFlag = val.name;
      if (val.list) {
        this.loginForm.userName = val.list.userName;
        this.loginForm.password = val.list.password;
      }
    },
    LoginBtn() {
      let _this = this;

      if (this.loginForm.userName === "" || this.loginForm.password === "") {
        this.$message({
          message: "用户名或密码不能为空",
          type: "warning",
        });
      } else {
        let obj = {
          userName: this.loginForm.userName,
          password: Encrypt(this.loginForm.password),
        };
        if (this.appType == "apphome") {
          login(obj).then((res) => {
            if (res.code == 200 && res.msg == "操作成功") {
              _this.$store.commit("SET_TOKEN", res.token);
              window.sessionStorage.setItem("SET_TOKEN", res.token);
              setToken(res.token);
              _this.$message.success("登录成功");
              _this.$router.push("/apphome");
            }
          });
        } else {
          _this.$store
            .dispatch("Login", {
              username: this.loginForm.userName,
              password: Encrypt(this.loginForm.password),
            })
            .then(() => {
              _this.$message.success("登录成功");
              _this.$router.push("/home");
            });
        }
      }
    },
  },
  mounted() {},
};
</script>
<style lang='scss' scoped>
.SignIn {
  width: 100vw;
  height: 100vh;
  font-size: 4vw;
  flex-direction: column;
  // background: linear-gradient(116deg, #41bb64 17%, #0a4fa0);
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  .topback {
    height: 40%;
    width: 100%;
    background: url("../../../assets/phone/AppVisitorHome/top_back.png")
      no-repeat;
    background-size: 100% 100%;
    position: absolute;
    top: 0;
  }
  .bottomTitle {
    font-size: 12px;
    color: #d1d1d1;
    position: absolute;
    bottom: 10vh;
    font-weight: 400;
    text-align: center;
  }
  .bottomback {
    height: 5%;
    width: 100%;
    background: url("../../../assets/phone/AppVisitorHome/bottom_back.png")
      no-repeat;
    background-size: 100% 100%;
    position: absolute;
    bottom: 0;
  }
  .SignInTitle {
    // width: 24vw;
    font-size: 8.5vw;
    font-weight: 800;
    text-align: center;
    padding-top: 4.47vh;
    box-sizing: border-box;
    margin: 0 auto;
    color: white;
    position: absolute;
    text-align: center;
    top: 0;
    width: 100%;
  }
  .SignInContent {
    width: 85vw;
    // height: 83vh;
    background-color: white;
    border-radius: 2vw;
    position: absolute;
    top: 20.29vh;
    padding-bottom: 5vh;
    border: 0.05vw solid #00000018;
    box-shadow: 0vh 0vw 5vw 3vw #00000018;
    .SignInContentBox {
      height: 100%;
      margin-top: 9.48vh;
      list-style: none;
      text-align: center;
      position: relative;
      li {
        margin-top: 5vw;
        position: relative;
        input {
          width: 70vw;
          border: 0.4vw solid rgb(153, 154, 155);
          background-color: #c7c5c521;
          border-radius: 4vw;
          padding: 2vw 0 2vw 4vw;
          font-weight: 400;
          font-size: 4.5vw;
        }
      }
      .SignInBtn {
        width: 75vw;
        height: 6vh;
        background: linear-gradient(110deg, #41bb64, #0a4fa0);
        color: white;
        text-align: center;
        border-radius: 7vw;
        font-size: 5vw;
        font-weight: 400;
        line-height: 6vh;
        margin: 6vw auto 0;
      }
      .registerBtn {
        width: 75vw;
        height: 5vh;
        color: #146296;
        border: 0.4vw #146296 solid;
        text-align: center;
        border-radius: 7vw;
        font-size: 5vw;
        font-weight: 400;
        line-height: 5vh;
        margin: 6vw auto 0;
        background-color: #c7c5c51c;
      }
      .LoginProblem {
        width: 85%;
        display: flex;
        justify-content: space-between;
        margin: 6vw auto 0;
        color: #146296;
        text-align: right;
      }
    }
    .TopImg {
      width: 20vw;
      height: 20vw;
      margin-top: 0 !important;
      position: absolute;
      top: -10vw;
      left: calc(50% - 10vw);
      background: url("../../../assets/phone/AppVisitorHome/icon_apphome.png") no-repeat;
      background-size: 100% 100%;
      img {
        width: 20vw;
      }
    }
  }
}
</style>