<template>
  <div class="SignIn">
    <div class="topback"></div>
    <div class="SignInTitle">自然保护区</div>
    <div class="SignInContent">
      <div class="TopImg">
        <!-- <img src="@/assets/phone/AppVisitorHome/icon_apphome.png" alt /> -->
      </div>
      <ul class="SignInContentBox">
        <li class="registerInput">
          <img src="@/assets/phone/deng/user.png" alt />
          <input
            type="text"
            placeholder="请输入用户名"
            v-model="RegisteredData.userName"
          />
        </li>
        <li class="registerInput">
          <img src="@/assets/phone/deng/user.png" alt />
          <input
            type="text"
            placeholder="请输入姓名"
            v-model="RegisteredData.fullName"
          />
        </li>
        <li class="registerInput">
          <img src="@/assets/phone/deng/phone1.png" alt />
          <input
            type="text"
            placeholder="请输入手机号"
            v-model="RegisteredData.phoneNumber"
          />
        </li>
        <li class="registerInput">
          <img src="@/assets/phone/deng/password1.png" alt />
          <input
            type="password"
            placeholder="请输入密码"
            v-model="RegisteredData.password"
          />
        </li>
        <li class="registerInput">
          <img src="@/assets/phone/deng/password2.png" alt />
          <input
            type="password"
            placeholder="请再次输入密码"
            v-model="RegisteredData.password1"
          />
        </li>
        <li>
          <input
            type="text"
            v-model="RegisteredData.code"
            class="codeInput"
            placeholder="请输入验证码"
          />
          <span class="countDown60" @click="countDown60">{{ content }}</span>
        </li>
        <div class="guarantee">
          <input type="checkbox" name id v-model="checkVal" />
          <span>
            我已阅读
            <span class="notice">《用户须知》</span>,并保证以上填写内容正确属实
          </span>
        </div>
        <li class="SignInBtn" @click="RegisteredAccount">注 册</li>

        <li class="have">
          已有账号，
          <span @click="loginFlagBtn">立即登录</span>
        </li>
      </ul>
    </div>
    <div class="bottomTitle">广西雅长兰科植物国家级自然保护区管理中心</div>
    <div class="bottomback"></div>
  </div>
</template>

<script>
import { verificationCode, addUser, isUserNameExist } from "@/api/case/case";
import { validatorPhone, cryptographicStrength } from "@/utils/validate";
export default {
  props: ["loginFlag"],
  data() {
    return {
      RegisteredData: {
        // 注册
        userName: "",
        fullName: "",
        phoneNumber: "",
        password: "",
        password1: "",
        code: "",
      },
      checkVal: false,
      content: "发送验证码",
      totalTime: 60,
      canClick: true, //添加canClick
    };
  },

  methods: {
    loginFlagBtn() {
      this.$emit("loginFlagFn1", { name: "SignIn" });
    },
    countDown60() {
      if (validatorPhone(this.RegisteredData.phoneNumber).flag) {
        if (!this.canClick) return;
        this.canClick = false;
        this.countDown();
        verificationCode(this.RegisteredData.phoneNumber)
          .then((res) => {
            this.$message({ message: res.msg, type: "success" });
          })
          .catch((err) => {
            if (err.message == "手机号已注册") {
              this.$message({ message: "手机号已注册", type: "warning" });
            }
          });
      } else {
        this.$message({
          type: "warning",
          message: validatorPhone(this.RegisteredData.phoneNumber).message,
        });
      }
    },
    countDown() {
      //倒计时
      this.content = this.totalTime + "s后重新发送";
      let clock = window.setInterval(() => {
        this.totalTime--;
        this.content = this.totalTime + "s后重新发送";
        if (this.totalTime < 0) {
          window.clearInterval(clock);
          this.content = "重新发送验证码";
          this.totalTime = 60;
          this.canClick = true; //这里重新开启
        }
      }, 1000);
    },
    RegisteredAccount() {
      //注册账号
      if (!this.checkVal) {
        this.$message({
          message: "请勾选我已阅读",
          type: "warning",
        });
        return;
      }
      let flag = this.$deepCopy(this.RegisteredData);
      if (
        flag.userName == "" ||
        flag.fullName == "" ||
        flag.phoneNumber == "" ||
        flag.password == "" ||
        flag.password1 == "" ||
        flag.code == ""
      ) {
        this.$message({
          message: "输入框不能为空",
          type: "warning",
        });
      } else {
        if (!validatorPhone(flag.phoneNumber).flag) {
          this.$message({
            message: validatorPhone(flag.phoneNumber).message,
            type: "warning",
          });
          return;
        }
        if (!cryptographicStrength(flag.password).flag) {
          this.$message({
            message: cryptographicStrength(flag.password).message,
            type: "warning",
          });
          return;
        }
        if (flag.password != flag.password1) {
          this.$message({
            message: "密码不一致",
            type: "warning",
          });
          return;
        }
        isUserNameExist({ userName: flag.userName }).then((res) => {
          if (res.data.code == "200") {
            let obj = {
              userName: flag.userName,
              fullName: flag.fullName,
              phoneNumber: flag.phoneNumber,
              password: flag.password,
              code: flag.code,
            };
            addUser(obj)
              .then((res) => {
                this.$message.success(res.msg);
                this.$emit("loginFlagFn1", {
                  name: "SignIn",
                  list: { userName: obj.userName, password: obj.password },
                });
              })
              .catch((err) => {
                this.$message.warning(err.message);
              });
          } else {
            this.$message.warning(res.data.msg);
            return;
          }
        });
      }
    },
  },
  mounted() {},
};
</script>
